.sup-menu {
  display: flex;
  align-items: flex-start; }
  .sup-menu .date {
    display: flex;
    align-items: center; }
  .sup-menu .bt-menu {
    margin-bottom: 5px !important; }
  @media (max-width: 1025px) {
    .sup-menu .box {
      width: 450px;
      max-width: 450px;
      display: flex;
      flex-wrap: wrap; } }

@media (max-width: 769px) {
  .sup-menu {
    flex-direction: column; }
    .sup-menu .box {
      width: 100%;
      min-width: 100%; } }
